.farmfood {
  margin-top: 30px;
  padding-top: 50px;
  padding-bottom: 20%;
  background: #f7f6f2;
  margin-bottom: 100px;
  .media(padding-bottom, 10%, 320px);
  .title {
    text-align: center;
    padding-bottom: 70px;
    .media-block( {
      padding-bottom: 50px;
    }
    , 320px);
    h6 {
      font-size: 12px;
      margin-bottom: 40px;
    }
    p {
      width: 84%;
      margin: auto;
    }
  }
  .nav{
justify-content: center;
position: relative;
.limon{
  width: 55%;
  top: 75%;
  position: absolute;
  margin: auto;
  .xl(top,85%);
  .md-block({
    width: 45%;
    top: 100%;
  });
  .sm(top, 97%);
}
    .nav-item{
      font-family: 'Lato';
      text-align: center;
      .md-block({
          display: flex;
      flex-direction: column
      }) ;
      width: 25%;
      .md-block({
        width: 40%;
        padding-bottom: 20px;
      });
      .sm(width,50%);
      img{
        display: block;
        margin: auto;
      }
      &:nth-child(3n + 1){
      margin-top: 70px;
      .md(margin, 0);
      }
     
      p{
        font-size: 14px;
        .md( margin-top, auto);
      }
      .btn{
        align-self: center;
      }
    }
  }
  
}










