.taste {
  .taste_title {
    text-align: center;
    margin-bottom: 5%;
    img {}
    .taste_text {
      display: flex;
      justify-content: center;
      .fa {
        color: #8dd3a5;
        font-size: 12px;
        margin: 0 3px;
        align-self: center;
      }
      h3 {
        font-size: 16px;
        font-weight: normal;
        margin: 0;
        span {
          font-weight: bold;
        }
      }
    }
  }
  .arrivals_title {
    font-family: 'Lato';
    .md-block( {
      text-align: center;
      margin-top: 20%;
    });
    h6 {
      font-size: 10px;
      color: #9f9f9f;
      letter-spacing: 1px;
    }
    p {
      color: #525252;
      font-size: 28px;
      font-weight: normal;
      .sm-block( {
        font-size: 20px;
      }
      );
      span {
        font-weight: bold;
      }
    }
  }
  .arrivals-slider {
    .media-block({
      text-align: center;
    },320px);
    button{
      position: absolute;
      top: -7%;
      left: 80%;
      width: 10%;
     border-radius: 20px;
     .lg-block({
      top: 0%;
      left: 40%;      
      width: 15%;
     });
    &.slick-prev{
      border: #9f9f9f 1px solid;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
      color: none;
      &::before{
        font-size: 14px;
        color: #525252;
    }
    }
    &.slick-next{
      .lg(margin-left,15%);
     margin-left: 10%;
      border: #9f9f9f 1px solid;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
      color: none;
      &::before{
        color: #525252;
        font-size: 14px;
    }
    }
   
    
    }
    .arrivals_card {
      display: flex !important;
      flex-flow: row !important;
      .md-block({
        flex-flow: column !important;
      });
      border: 1px solid #9f9f9f;
      padding: 5px;
      border-radius: 10px;
      margin-top: 25px;
      .card_img {
        img {
          .md-block( {
            margin: auto;
            ;
          }
          );
        }
      }
      .card_title {
      display: flex;
      flex-direction: column;
      align-self: center;
        .md-block( {
          text-align: center;
        }
        );
        h3 {
          font-weight: normal;
          font-size: 18px;
          color: #525252;
          .lg(font-size, 12px);
          span {
            font-weight: bold;
          }
        }
        p {
          font-size: 14px;
          color: #7fbb00;
        }
      }
    } 
    
    
  
  }
  .banner {
    border-radius: 10px;
    margin-top: 5%;
    position: relative;
    background: #d7edcd;
    padding: 60px 35px 35px 40px;
    display: flex;
    h5 {
      position: absolute;
      color: #a7c57f;
      font-size: 12px;
      font-weight: normal;
      margin-left: 35%;
      top: 10%;
      .md(margin-left,22%);
      .media-block( {
        top: 5%;
        font-size: 11px;
      }
      , 320px);
      span {
        font-weight: bold;
      }
    }
    .img1 {
      position: absolute;
      top: 0%;
      left: 0;
     .lg(width,30%);
    }
    .vegetable {
      position: relative;
      border: 5px solid #fff;
      padding: 35px 30px;
      width: 100%;
      .lg-block( {
        padding: 25px 15px;
      }
      );
      .sm-block( {
        padding: 25px 25px;
      }
      );
      .media-block( {
        padding: 45px 15px;
      }
      , 320px);
      h2 {
        color: #b8c800;
        font-family: 'Lato';
        .media-block( {
          font-size: 26px;
        }
        , 320px);
      }
      p {
        font-size: 28px;
        font-family: 'Lato';
        font-weight: 300;
        span {
          font-weight: 700;
        }
        .media-block( {
          font-size: 24px;
        }, 320px);
    }
      h6 {
        font-size: 11px;
        font-family: 'Lato';
        font-weight: normal;
        letter-spacing: 2px;
      }
      .img2 {
        position: absolute;
        right: -5%;
        bottom: -4%;
        width: 55%;
   .xl(width, 52%);
   .lg(width, 48%);
   .sm(width, 40%);
      }
    }
    .btn {
      font-size: 14px;
      position: absolute;
      background: #b8c800;
      color: #fff;
      border-radius: 50px;
      border: 5px solid #fff;
      padding: 5px 14px;
     bottom: 5%;
     left: 12%;
      font-weight: bold;
      font-family: "Lato";
  
      .md-block( {
        font-size: 12px;
      }
      );
     
      i {
        margin-left: 18%;
        margin-right: 18px;
 
      }
    }
  }
  .banner2 {
    border-radius: 10px;
    margin-top: 5%;
    position: relative;
    background: #fbefa1;
    padding: 60px 35px 35px 40px;
    display: flex;
    margin-bottom: 10%;
    h5 {
      position: absolute;
      color: #afa765;
      font-size: 12px;
      font-weight: normal;
      text-transform: uppercase;
      top: 10%;
      span {
        font-weight: bold;
      }
    }
    .fruits {
      position: relative;
      border: 5px solid #fff;
      padding: 35px 30px;
      width: 100%;
      .lg-block( {
        padding: 25px 15px;
      }
      );
      .sm-block( {
        padding: 25px 25px;
      });
      .media-block( {
        padding: 45px 15px;
      }, 320px);
      h2 {
        text-transform: capitalize;
        color: #ffbe00;
        font-family: 'Lato';
        .media-block( {
            font-size: 26px;
          }
          , 320px);
      }
      p {
        text-transform: uppercase;
        font-size: 28px;
        font-family: 'Lato';
        font-weight: 300;
        .media-block( {
            font-size: 24px;
          }, 320px);
        span {
          font-weight: 700;
        }
      }
      h6 {
        font-size: 11px;
        font-family: 'Lato';
        font-weight: normal;
        letter-spacing: 2px;
      }
      .img2 {
        position: absolute;
        right: -9%;
        bottom: -15%;
        width: 70%;
        .xl-block({
          right: -11%;
          bottom: -10%;
          width: 60%;
        });
        .lg(right, -8%);
        .media-block({
       right: -12%;
      },767px);
       
        .sm-block({
          right: 0;
          width: 50%;
          top: -15%;
        });
      }
    }
    
    .btn {
      font-size: 14px;
      position: absolute;
      background: #ffbd0e;
      color: #fff;
      border-radius: 50px;
      border: 5px solid #fff;
      padding: 5px 14px;
     bottom: 5%;
     left: 12%;
      font-weight: bold;
      font-family: "Lato";
  
      .md-block( {
        font-size: 12px;
      }
      );
     
      i {
        margin-left: 18%;
        margin-right: 18px;
 
      }
    }
    
   
  }
 
}