.newsletter {
  background: #f7f6f2;
  padding-top: 70px;
  padding-bottom: 100px;
  position: relative;
  margin-bottom: -5%;
  .sm(margin-bottom, 0);
  
  .md-block( {
    padding-top: 50px;
    padding-bottom: 70px;
    margin-bottom: 0;
  }
  );
  .media-block( {
    padding-bottom: 35px;
    padding-top: 40px;
    margin-bottom: 0;
  }
  , 320px);
  .img1 {
    position: absolute;
    left: 0%;
    bottom: 0;
   width: 15%;
  }
  .img2 {
    position: absolute;
    right: 0%;
    top: 0;
  width: 20%;
  }
  .subscribe{
    display: flex;
    justify-content: right;
    .md-block({
      flex-direction: column;
    });
    .title {
      width: 30%;
      .md-block({
        width: 100%;
      text-align: center;
      });
    font-family: 'Lato';
    color: #525252;
    text-align: left;
    h6 {
      font-size: 11px;
      letter-spacing: 2px;
    }
    p {
      font-family: 'Roboto';
      font-size: 24px;
    }
  }
  form {
    .md-block({
      width: 90%;
      justify-content: center;
      margin:  auto;
    });
    width: 60%;
    .form-control {
      width: inherit;
      margin-right: 5%;
      border-radius: 20px;
      border: 1px solid white;
      background: #fff;
      padding-left: 12px;
    }
    .btn {
      background: #7cc000;
      border-radius: 20px;
      border: 1px solid #7cc000;
      padding: 0.575rem 1.35rem;
      text-transform: lowercase;
      font-size: 12px;
      color: #fff;
      .fa {
        margin-left: 5px;
      }
    }
  }
  }
  

}