.organic-data {
  padding: 70px 0;
  background: #f7f6f2;
  .slider {
    .item {
      display: flex;
      font-family: 'Roboto';
      .lg-block( {
        flex-direction: column;
        text-align: center;
      }
      );
      .title {
        width: 40%;
        text-align: right;
        align-self: center;
        .lg-block( {
          width: auto;
          text-align: center;
          order: 2;
        }
        );
        h3 {
          padding-top: 1%;
          font-size: 30px;
        }
      }
      img {
        width: 20%;
        .lg-block( {
          width: auto;
          align-self: center;
          order: 1;
        }
        );
      }
      .text {
        .lg-block( {
          width: auto;
          order: 3;
        }
        );
        width: 40%;
        align-self: center;
        font-size: 16px;
      }
    }
    .slick-dots {
      li {
        height: 2px;
        width: 15%;
        background: #fff;
        &.slick-active {
          background: #7dbe00;
        }
        button {
          display: none;
        }
      }
    }
  }
  
.fresh{
 margin-top: 70px;
  .item{
    width: 50%;
    display: flex;
    .sm-block({
      flex-direction: column-reverse;
    });
    .card_icons{
      display: flex;
      flex-direction: column;
      width: 10%;
      text-align: center;
      .sm-block({
        width: 75%;
        flex-direction: row;
        margin:  auto;
        justify-content: space-around;
      
      });
      .fa{
        .media-block({
          line-height: 3.2;
        },991px);
        .md(line-height, 3.5);
        .sm-block({
            line-height: 1.5;
            width: 34%;
        });
        font-size: 24px;
        line-height: 3.5;
        background: #fff;
        border: 1px solid #f7f6f2;
        color: #ddd;
        &:nth-child(1) {
          border-top-left-radius: 10px;
          &:hover {
            color: pink;
          }
        }
        &:nth-child(2) {
          &:hover {
            color: grey;
          }
        }
        &:nth-child(3) {
          border-bottom-left-radius: 10px;
          &:hover {
            color: #79d1d6;
          }
        }
      }
    }
    .card_title{
      .sm-block({
        margin: auto;
        width: 75%;
        padding-left: 0;
        text-align: center;
      });
      height: inherit;
      padding-top: 40px;
      padding-left: 20px;
      width: 50%;
      background: #fff;
      h6{
        font-size: 10px;
      }
      h3{
        font-size: 30px;
      }
      p{
      font-size: 14px;
      margin: 1.6rem 0 ;
      }
      .price{
        color: #7dbe00;
        span {
          color: #ddd;
        }
      }
    }
    .card_img{
      .sm-block({
        width: 75%;
        margin:  auto;;
      });
      img{
        .sm-block({
          margin: auto;
        });
      }
      width: 35%;
      position: relative;
      .buttons{
        padding: 20px 0 15px 0;
        background: #fff;
        text-align: center;
        button{
          border: none;
          font-family: 'Lato';
          text-transform: uppercase;
          padding: 0px 15px;
          border-radius: 20px;
          color: #fff;
          font-size: 12px;
          &.new {
            background: #92d2a7;
          }
          &.sale {
            background: #79d1d6;
          }
        }
      }
      
    }
  }
  .slick-dots{
    bottom: -50px;
    li{
      button{
        font-size: 13px;
        color: #cccccc;
        &::before{
          display: none;
        }
      }
     &.slick-active{
       background: #79d1d6;
       border-radius: 50%;
       button{
       color: #fff;
       }
     }
    }
  }
}
  
}
