header {
  position: relative;
  .navbar {
    position: absolute;
    top: 5%;
    width: 100%;
    padding: 0 65px;
    .media-block( {
      padding: 0 10px;
    }
    , 440px);
    .navbar-brand {
      img{
        .media-block({
          width: 50%;
        },440px);
      }
     
    }
  .navbar-toggler {
    .media-block({
      font-size: 12px;
      padding: 1px 3px;
    },440px)
   
  }
  
    .navbar-collapse {
      align-self: flex-end;
      .navbar-nav {
        .nav-item {
          .media-block( {
            align-self: flex-start;
          }
          , 440px);
          .nav-link {
            font-size: 15px;
            color: @font;
            font-family: 'Raleway';
            text-transform: uppercase;
            .media-block({
            font-size: 12px;
            padding: 0;
            },440px);
            &:after {
              content: "";
              display: block;
              width: 6px;
              height: 6px;
              background: @green;
              border-radius: 50%;
              margin: auto;
              opacity: 0;
            }
            &:hover::after {
              opacity: 1;
            }
          }
        }
      }
    }
  }
 .carousel {
 
    .indicators{
      position: absolute;
      top: 89%;
      right: 8%;
      .media-block({
        top: 85%;
      },320px);
      ol {
        li {
          text-indent: 1px;
          background: 0;
          position: absolute;
          display: none;
          &::after {
            content: "/05";
            font-size: 10px;
            color: @font;
            bottom: -10px;
            left: 20px;
            .media-block( {
              font-size: 9px;
              left: 15px;
            }
            , 320px);
          } 
        }
        .active {
          background: 0;
          display: block;
          color: @green;
          font-weight: 700;
          font-size: 16px;
          .media-block( {
            font-size: 12px;
            margin-top: 5px;
          }
          , 320px);
        }
      } 
      .fa {
        font-size: 14px;
        color: #b1b1b1;
        width: 30px;
        height: 20px;
        padding:3px;
        border-radius: 20px;
        border: 1px solid @white;
       background-color: @white;
        &.fa-long-arrow-left{
          border-top-right-radius: 0;
          border-bottom-right-radius: 0;
        }
        &.fa-long-arrow-right{
          border-top-left-radius: 0;
          border-bottom-left-radius: 0;
        }
        .media-block({
          width: 20px;
          height: 15px;font-size: 10px;
        },320px)
      }
    }
   
   
    .carousel-caption {
      margin-top: 10%;
      bottom: 35%;
      .lg(bottom, 25%);
      .md(bottom, 15%);
      .media-block( {
        bottom: 5%;
      }
      , 440px);
      h1 {
        .lg(font-size, 42px);
        .md(font-size, 36px);
        font-size: 70px;
        text-transform: capitalize;
        font-weight: 300;
        color: @font;
        .sm-block( {
          font-size: 22px;
        }
        );
        .media-block( {
          font-size: 14px;
        }
        , 440px);
        span {
          font-weight: 700;
        }
      }
      p {
        font-size: 14px;
        font-weight: 500;
        letter-spacing: 5px;
        color: @font;
        .lg(font-size, 13px);
        .md-block( {
          font-size: 12px;
          letter-spacing: 1px;
        }
        );
        .sm-block( {
          font-size: 10px;
          letter-spacing: 0px;
        }
        );
      }
      .btn {
        background: #ffffff;
        color: #444444;
        padding: 14px 34px;
        border: 5px solid #5ec6ce;
        border-radius: 40px;
        border-block-start-color: #a4d2a1;
        border-left-color: 50% #a4d2a1;
        border-right-color: 50% #a4d2a1;
        .lg(padding, 10px 25px);
        .md-block( {
          font-size: 12px;
          padding: 7px 16px;
          font-weight: bold;
        }
        );
        .sm-block( {
          font-size: 10px;
          padding: 6px 6px;
        }
        );
        .media-block( {
          bottom: 26%;
          padding: 3px 3px;
        }
        , 440px);
      }
    }
  }
}