.organic {
  transition: all 0.2s;
  .nav {
    justify-content: space-between;
    li {
      align-self: center;
      text-align: center;
      width: 35%;
      &:nth-child(2) {
       width: 30%;
        ;
      }
      .lg-block( {
        display: flex;
        flex-direction: column;
        &:nth-child(2) {
          display: block;
        }
      }
      );
      .md-block( {
        width: 30%;
      }
      );
      .sm-block( {
        order: 2;
        width: 50%;
        &:nth-child(2) {
          order: 1;
          width: 80%;
          margin: auto;
          ;
        }
        ;
      }
      );
      a {
        font-size: 16px;
        color: #9f9e9c;
        text-decoration: none;
        border: 1px solid #d8d8d7;
        border-radius: 40px;
        margin: 0px 5px;
        padding: 10px 10px;
        &.active {
          background: yellowgreen;
          color: #f1f1f1;
        }
        &:hover {
          background: yellowgreen;
          color: #f1f1f1;
        }
        .lg-block( {
          margin: 5px;
          text-align: center;
          padding: 10px 15px;
        }
        );
        .md(font-size, 12px);
        .sm-block( {
          padding: 10px;
        }
        )
      }
    }
  }
  .tab-content {
    .tab-pane {
      &.lazy {
       
        &.active {
          padding: 15px;
          margin-left: 15px;
          margin-right: 15px;
        }
        .slick-slide {
          margin: 0 15px;
          .media-block( {
            margin: 0 45px;
          }
          , 400px);
          &.card {
            box-shadow: none;
            text-align: center;
            border-radius: 20px;
            background: url('../img/cardbg.png') center center no-repeat;
            &::after {
              content: " ";
              width: 30%;
              height: 3px;
              background: #81ba00;
              display: block;
              margin: auto;
              opacity: 0;
              transition: 1.5s;
            }
            .card-img-top {
              height: auto;
              padding-top: 15px;
            }
            .card-body {
              .card-title {
                font-weight: normal;
                font-family: 'Roboto';
                font-size: 16px;
                .md(font-size,
                12px);
                span {
                  font-weight: bold;
                }
                .media-block( {
                  font-size: 18px;
                }
                ,
                320px);
              }
              .card-text {
                font-family: 'Roboto';
                color: #81ba00;
                font-weight: bold;
              }
            }
            &:hover {
              box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
              border-bottom: 1px solid #9f9e9c;
              .icons {
                opacity: 1;
                text-decoration: none;
                .fa {
                  opacity: 1;
                }
              }
            }
            &:hover::after {
              opacity: 1;
            }
          }
        }
        .slick-dots {
          text-align: center;
          li {
            button {
              display: none;
            }
            width: 53px;
            height: 8px;
            background: #f7f6f2;
            margin: 0 2px;
            &.slick-active {
              background: #92d2a7;
            }
          }
        }
      }
    }
  }
}