.naturix{
  padding: 30px 0;
  .title{
   text-align: center;
   margin-bottom: 50px;
   h6 {
    font-size: 12px;
    letter-spacing: 2px;
    margin-bottom: 20px;
  }
  h3 { 
    display: inline-block;
    border: 1px solid #e6e6e6;
    padding: 8px 30px;
    border-radius: 50px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      display: block;
      width: 50%;
      height: 1px;
      background: #e6e6e6;
      left: -50%;
      top: 50%;
    }
    &::before {
      content: "";
      position: absolute;
      display: block;
      width: 50%;
      height: 1px;
      background: #e6e6e6;
      right: -50%;
      top: 50%;
    }
  }
   
  }
  
  .nav {
    justify-content: center;
    li {
    display: flex;
      .lg-block( {
       
      });
      .md-block( {
      
      });
      .media-block( {
flex-direction: column;
      },400px);
      a {
        font-size: 16px;
        color: #9f9e9c;
        text-decoration: none;
        border: 1px solid #d8d8d7;
        border-radius: 40px;
        margin: 0px 5px;
        padding: 10px 10px;
        &.active {
          background: yellowgreen;
          color: #f1f1f1;
        }
        &:hover {
          background: yellowgreen;
          color: #f1f1f1;
        }
        .lg-block( {
          margin: 5px;
          text-align: center;
          padding: 10px 15px;
        }
        );
        .md(font-size, 12px);
        .sm-block( {
          padding: 10px;
        }
        )
      }
    }
  }
  
  .tab-content {
      .multiple-items {
  
        &.active {
          padding: 15px;
          margin-left: 15px;
          margin-right: 15px;
        } 
        .slick-slide {  
          margin: 20px 15px;
          .media-block({
            margin: 0 45px;
          },400px);  
          .card {
           /*  .media-block({
              width: 185px !important;
            },400px); */
            
            border-radius: 20px;
            margin-top: 30px;
            box-shadow: none;
            text-align: center;
            background: url('../img/cardbg.png') center center no-repeat;
            &::after{
              content: " ";
              width: 30%;
              height: 3px;
              background: #81ba00;
              display: block;
              margin: auto;
              opacity: 0;
               transition: 1.5s;
            }
            .card-img-top {
              height: auto;
              padding-top: 15px;
             
            }
            .card-body {
              .card-title {
                font-weight: normal;
                font-family: 'Roboto';
                font-size: 16px;
                .md(font-size,
                12px);
                span {
                  font-weight: bold;
                }
                .media-block( {
                  font-size: 18px;
                }
                ,
                320px);
              }
              .card-text {
                font-family: 'Roboto';
                color: #81ba00;
                font-weight: bold;
              }
            }
            &:hover {
              box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
              border-bottom: 1px solid #9f9e9c;
              .icons {
                opacity: 1;
                text-decoration: none;
                .fa {
                  opacity: 1;
                }
                
              }
            }
            &:hover::after {
              opacity: 1;
            }
          }
        }
        .slick-dots {
          text-align: center;
          li {
            button{
              display: none;
            }
            width: 53px;
            height: 8px;
            background: #f7f6f2;
            margin: 0 2px;
            &.slick-active {
              background: #92d2a7;
            }
         
          }
        }
      }
  }  
}
