header {
  position: relative;
}

header .navbar {
  position: absolute;
  top: 5%;
  width: 100%;
  padding: 0 65px;
}

header .navbar .navbar-collapse {
  align-self: flex-end;
}

header .navbar .navbar-collapse .navbar-nav .nav-item .nav-link {
  font-size: 15px;
  color: #000;
  font-family: 'Raleway';
  text-transform: uppercase;
}

header .navbar .navbar-collapse .navbar-nav .nav-item .nav-link:after {
  content: "";
  display: block;
  width: 6px;
  height: 6px;
  background: #81ba00;
  border-radius: 50%;
  margin: auto;
  opacity: 0;
}

header .navbar .navbar-collapse .navbar-nav .nav-item .nav-link:hover::after {
  opacity: 1;
}

header .carousel .indicators {
  position: absolute;
  top: 89%;
  right: 8%;
}

header .carousel .indicators ol li {
  text-indent: 1px;
  background: 0;
  position: absolute;
  display: none;
}

header .carousel .indicators ol li::after {
  content: "/05";
  font-size: 10px;
  color: #000;
  bottom: -10px;
  left: 20px;
}

header .carousel .indicators ol .active {
  background: 0;
  display: block;
  color: #81ba00;
  font-weight: 700;
  font-size: 16px;
}

header .carousel .indicators .fa {
  font-size: 14px;
  color: #b1b1b1;
  width: 30px;
  height: 20px;
  padding: 3px;
  border-radius: 20px;
  border: 1px solid #fff;
  background-color: #fff;
}

header .carousel .indicators .fa.fa-long-arrow-left {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

header .carousel .indicators .fa.fa-long-arrow-right {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

header .carousel .carousel-caption {
  margin-top: 10%;
  bottom: 35%;
}

header .carousel .carousel-caption h1 {
  font-size: 70px;
  text-transform: capitalize;
  font-weight: 300;
  color: #000;
}

header .carousel .carousel-caption h1 span {
  font-weight: 700;
}

header .carousel .carousel-caption p {
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 5px;
  color: #000;
}

header .carousel .carousel-caption .btn {
  background: #ffffff;
  color: #444444;
  padding: 14px 34px;
  border: 5px solid #5ec6ce;
  border-radius: 40px;
  border-block-start-color: #a4d2a1;
  border-left-color: 50% #a4d2a1;
  border-right-color: 50% #a4d2a1;
}

.organic {
  transition: all 0.2s;
}

.organic .nav {
  justify-content: space-between;
}

.organic .nav li {
  align-self: center;
