  box-shadow: none;
  text-align: center;
  border-radius: 20px;
  background: url('../img/cardbg.png') center center no-repeat;
}

.organic .tab-content .tab-pane.lazy .slick-slide.card::after {
  content: " ";
  width: 30%;
  height: 3px;
  background: #81ba00;
  display: block;
  margin: auto;
  opacity: 0;
  transition: 1.5s;
}

.organic .tab-content .tab-pane.lazy .slick-slide.card .card-img-top {
