}

.organic .tab-content .tab-pane.lazy .slick-slide.card:hover {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  border-bottom: 1px solid #9f9e9c;
}

.organic .tab-content .tab-pane.lazy .slick-slide.card:hover .icons {
  opacity: 1;
  text-decoration: none;
}

.organic .tab-content .tab-pane.lazy .slick-slide.card:hover .icons .fa {
  opacity: 1;
}

.organic .tab-content .tab-pane.lazy .slick-slide.card:hover::after {
