  width: 53px;
  height: 8px;
  background: #f7f6f2;
  margin: 0 2px;
}

.organic .tab-content .tab-pane.lazy .slick-dots li button {
  display: none;
}

.organic .tab-content .tab-pane.lazy .slick-dots li.slick-active {
  background: #92d2a7;
}

