footer {
  .logo_here {
    .nav-item {
     &:nth-child(1){
       &::after{
         content: "";
         display: block;
         width: 36px;
         height: 2px;
         background: #81ba00;
        
         margin: 20px 0;
       }
     }
      .navbar-brand {
        font-size: 18px;
      }
      .brand {
        font-size: 30px;
      }
      .nav-link {
        color: #525252;
        font-size: 14px;
        .fa {
          margin-right: 5%;
        }
        p{
          line-height: 1;
        }
        .green {
          color: #7ac200;
        }
        .list {
          margin-left: 0%;
        }
      }
    }
  }
  .widget_photostream {
    padding-top: 2%;
    .sm(text-align,center);
    li {
      list-style: none;
      a {
        padding: 0;
      }
    }
    h5 {
      font-size: 18px;
      &::after{
        content: "";
        display: block;
        width: 36px;
        height: 2px;
        background: #81ba00;
        margin: 20px 0;
      }
    }
    .photostream {
      list-style: none;
      margin: 18px 0 24px -15px;
      padding: 0;
      overflow: hidden;
      .sm(padding-left,30px);
      &::after {
        content: " ";
        display: table;
      }
      i {
        list-style: none;
        hr {
          width: 30px;
          background: #7ac200;
          display: inline-block;
          font-weight: bold;
        }
      }
      li {
        float: left;
        display: inline-block;
        margin: 0 0 15px 15px;
        padding: 0;
      }
    }
  }
}