@import "./header.less";
@import "./params/mix.less";
@import "./params/variables.less";
@import "./organic.less";
@import "./farmfood.less";
@import "./fruits.less";
@import "./naturix.less";
@import "./organic_data.less";
@import "./taste.less";
@import "./newsletter.less";
@import "./users-slider.less";
@import "./naturix_blog.less";
@import "./footer.less";
body {
  min-width: 320px;
  
  #top {
    display: none;
    position: fixed;
    right: 45%;
    bottom: 2%;
    cursor: pointer;
    z-index: 10; 
   padding-top: 0.5%;
   padding-left: 1%;
    border-radius: 35px;
    width: 40px;
    height: 35px;
  }

  p {
    font-size: 16px;
    color: #525252;
    font-weight: normal;
    font-family: 'Raleway';
  }
  h6 {
    font-size: 12px;
    color: #525252;
    font-weight: normal;
    font-family: 'Raleway';
    text-transform: uppercase;
  }
  h3 {
    font-size: 40px;
    color: #525252;
    font-weight: normal;
    font-family: 'Raleway';
  }
  span {
    font-weight: bold;
  }
  .list {
    position: relative;
    margin-left: -1%;
    .media-block( {
      margin-left: -4px;
    }
    , 320px);
    &::after {
      content: "";
      position: absolute;
      display: block;
      width: 23px;
      height: 35px;
      background: url('../img/list.png');
      right: -70%;
      bottom: 65%;
      .media-block( {
        width: 24px;
        height: 38px;
        top: -18px;
        margin: -1px;
      }
      ,
      320px);
    }
  }
  .icons {
    transition: 1.5s;
    opacity: 0;
    display: flex;
    justify-content: center;
    a {
      width: 30px;
      height: 24px;
      border: 1px solid none;
      text-decoration: none;
      background: #fff;
      color: #dedede;
      &.fa-heart {
        border-radius: 20px;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        padding: 3px 8px;
        &:hover {
          color: pink;
        }
      }
      &.fa-shopping-basket {
        padding: 3px 5px;
        &:hover {
          color: grey;
        }
      }
      &.fa-search-plus {
        padding: 3px;
        border-radius: 20px;
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        &:hover {
          color: #79d1d6;
        }
      }
    }
  }
 .fix{
   background: #81ba00;
   box-shadow: 0 10px 15px #525252;
   position: fixed;
   width: 100%;
   z-index: 999;
   font-family: 'Raleway';
   top: 0;
 }
}

img {
  max-width: 100%;
  height: auto;
}