    .users-slider {
    .lg(padding, 55px 0);
    .md(top, 100%);
    .lg(width, 100%);
    padding: 80px 0px;
    background: #fff;
    width: 80%;
    border-radius: 20px;
    margin: auto;
    .record {
      .md(flex-direction, column);
      display: flex;
      justify-content: center;
      .img {
        .md(align-self, center);
        img {
          border: 4px solid #90d3a6;
          border-radius: 50%;
          max-width: 100px;
        }
      }
      .comment {
        width: 60%;
        .md(width, 90%);
        .md(align-self, center);
        margin-left: 20px;
        text-align: left;
        display: flex;
        flex-direction: column;
        p {
          font-family: 'Montserrat';
          font-size: 14px;
          font-style: italic;
          padding: 0 5px;
          
        }
        a {
          text-decoration: none;
          color: #525252;
          text-transform: uppercase;
          font-weight: bold;
          font-family: 'Lato';
          i {
            color: #b8ca00;
            margin-right: 1.5%;
          }
        }
      }
    }
    button{
      top: 100%;
      left: 46%;
      width: 4%;
      height: 20px;
      border-radius: 20px;
      padding: 3px 9px;
      border: 1px solid #b1b1b1;
      background: @white;
      .lg(width,5%);
      .md(width,7%);
      .sm(width,8%);
      .media-block({
        width: 10%;
      },500px);
      &::before {
        font-size: 12px;
        color: #b1b1b1;
      }
      
      &.slick-prev {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
        &::after {
          content: "";
          display: block;
          width: 380px;
          height: 1px;
          background: #b1b1b1;
          position: absolute;
          right: 100%;
          top: 50%;
        } 
      }
      &.slick-next{
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        margin-left: 4%;
        .lg(margin-left, 5%);
        .md(margin-left, 7%);
        .sm(margin-left, 8%);
        .media-block({
          margin-left: 10%;
        },500px);
        &::after {
          content: "";
          display: block;
          width: 380px;
          height: 1px;
          background: #b1b1b1;
          position: absolute;
          left: 100%;
          top: 50%;
        } 
      }  
    }
    
  }
