.naturix_blog{
    padding-top: 60px;
    padding-bottom: 120px;
 .title{
    text-align: center;
    margin-bottom: 5%;
 } 
 .foods{
     display: flex;
     justify-content: space-evenly;
     .md( flex-direction, column);
    .blog{
        justify-content: space-between;
         display: flex;  
         width: 45%;
         .lg-block({
           flex-direction: column;
         });
         .md(margin, auto);
         .media-block({
            width:65%;  
        },430px);
         .blog_img{
            .lg-block({
                width:60%;
                height: 220px;
                margin: auto;;
             });
             .media-block({
                width:100%;  
            },767px);
             .md(width, 80%);
             border: 1px solid #9f9f9f;
             border-radius: 15px;
             position: relative;
             margin-bottom: 15px;
             width: 45%;
             height: 90%;
             img{
                 width: 240px;
                 height: 220px;
                 position: absolute;
                border-radius: 15px;   
                margin-top: 10px;
                margin-left: 10px; 
             }
         }
         .blog_title{
             padding: 15px 0;
            font-family: 'Roboto';
           width: 48%;
           .lg-block({
            width:60%;
            margin: auto;;
         });
         .md-block({
             width: 80%;
             padding-top: 10%;
         });
         .media-block({
             width:100%;  
         },767px);
           h5{
         font-size: 10px;  
         color: #9f9f9f;    
           }  
           h2{
               font-size: 22px;
               color: #525252;
               margin-bottom: 18%;
               .md(margin-bottom,10%);
           }
           p{
              font-size: 12px; 
              color: #525252;
              margin-bottom: 10%;
           }
           .btn{
            font-size: 13px;   
            border: none;
            color: #84c400;
            &:hover{
                background: #84c400;
                color: #fff;
            }
           }
         }
     }   
 }  
 
}