.fruits {

  .nav{
    .md(justify-content,center);
    &:nth-child(1){
      margin-bottom: 60px;
    }
    li{
      margin: 10px;
      padding: 15px 0;
      text-align: center;
      font-family: 'Roboto';
      border: #efefef 1px solid;
      border-radius: 10px;
      flex-basis: 160px;
      flex-wrap: wrap;
      .md(flex-basis,140px);
      &.active{
        border: 2px solid #a8d355;
        border-radius: 10px;
      }
      a{
        text-decoration: none;
        color: #525252;
        h5{
          font-size: 1.20rem;
          &:before{
            content: "";
            display: flex;
            width: 20%;
            height: 1px;
            background: #eeeeee;
            margin: 15px auto;
          }  
        }
        p{
          color: #a8d355;
          
        }
      }
    }
  }
  .tab-content{
    &:nth-child(1){
      margin-bottom: 80px;
    }
    .tab-pane{
      h6{
        font-size: 12px;
      }
      h3{
        font-size: 35px;
        padding-top: 3%;
        padding-bottom: 5%;
      }
      p{
        font-size: 16px;
      }
    }
  }
  
}


